<template>
  <div class="product_list">
    <div class="product_main">
      <cata-log :page="page"></cata-log>
      <div class="cart_list">
        <div
          class="cart_content"
          v-for="(item, index) of showCarts"
          :key="index"
        >
          <input
            class="check"
            type="checkbox"
            name="cb1"
            value=""
            :checked="item.selectStatus == '1' ? true : false"
            @click="changeStatus(item)"
          />

          <div class="product_info">
            <img class="info_img" src="../../assets/img/pp.png" />
            <div class="info_content">
              <div class="info_content_title">
                这里是标题信息这里是标题信息这里是标题信息
              </div>
              <div class="info_content_txt">2020/02/09</div>
              <div class="info_content_txt">
                这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息…
              </div>
              <div class="info_content_txt">分发类型</div>
              <div class="cart_btn">
                <img class="all_cart" src="../../assets/img/01@3x.png" alt="" />
                <div class="add_num">
                  <div class="add_num_btn" @click="addCart(item, -1)"></div>
                  <div class="add_num_num">{{ item.quantity }}</div>
                  <div class="add_num_btn" @click="addCart(item, 1)"></div>
                </div>
                <div class="btn_del" @click="delCart(item)">
                  <img src="../../assets/img/del.png" alt="" /> 删除
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="admit" @click="submit()">下单</div>
      </div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
import CataLog from "/page/mine/catalog";
import {
  queryCart,
  addCart,
  deleteCart,
  submitCart,
  changeCart,
} from "/api/user.js";
export default {
  data() {
    return {
      page: "cart",
      showCarts: [],
    };
  },
  methods: {
    // 查询购物车
    queryCart() {
      let that = this;
      queryCart().then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          this.showCarts = res.data;
        }
      });
    },
    addCart(item, num) {
      if (item.quantity < 1 && num < 0) {
        return;
      }
      let paramsAdd = new FormData();
      paramsAdd.append("productId", item.productId);
      paramsAdd.append("quantity", num);
      paramsAdd.append("selectStatus", 1);
      addCart(paramsAdd).then((res) => {
        if (res && res.code && res.code == "200") {
          this.queryCart();
        }
      });
    },
    delCart(item) {
      let paramsDel = new FormData();
      paramsDel.append("productIds", item.productId);
      deleteCart(paramsDel).then((res) => {
        if (res && res.code && res.code == "200") {
          this.queryCart();
        }
      });
    },
    // 下单
    submit() {
      let productIds = ''
      let showCarts = this.showCarts
      showCarts.forEach(item => {
        if(item.selectStatus == '1'){
          productIds+=`${item.productId},`
        }
      });
      let paramsAll = new FormData();
      paramsAll.append("deliveryType", 1);
      paramsAll.append("productIds", productIds);
      paramsAll.append("usePlan", '十多年撒局从v你哦荻港VB独爱世博会圣诞红包');
      paramsAll.append("userMobile", '15623354563');
      paramsAll.append("userName", '王婷婷');
      submitCart(paramsAll).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
        }
      });
    },
    // 改变状态
    changeStatus(item) {
      let status = 1 - item.selectStatus * 1;
      let paramsC = new FormData();
      paramsC.append("productId", item.productId);
      paramsC.append("quantity", item.quantity);
      paramsC.append("selectStatus", status);
      changeCart(paramsC).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          this.queryCart()
        }
      });
    },
  },
  mounted() {
    this.queryCart();
  },
  created() {},
  components: {
    CataLog,
  },
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.product_list {
  background: #f0faff;
  width: 1440px;
  margin: 0 auto;
  padding-top: 24px;
}
.bottom {
  height: 24px;
}
.product_main {
  width: 1200px;
  background: #ffffff;
  margin: 0 auto;
  padding: 1px 0;
  display: flex;
  .cart_list {
    width: 946px;
    padding-left: 40px;
    .cart_content {
      width: 906px;
      height: 230px;
      border-bottom: 1px solid #ededed;
      display: flex;
      .check {
        margin-top: 108px;
        margin-right: 16px;
        cursor: pointer;
      }
      .product_info {
        width: 864px;
        height: 230px;
        box-shadow: 0 1px 0 0 #ededed;
        display: flex;
        padding: 24px 0;
        .info_img {
          width: 220px;
          height: 182px;
        }
        .info_content {
          width: 603px;
          margin-left: 20px;
        }
        .info_content_title {
          font-size: 16px;
          color: #333333;
          font-weight: bold;
          height: 22px;
          line-height: 22px;
          margin-bottom: 8px;
        }
        .info_content_txt {
          font-size: 14px;
          color: #787878;
          line-height: 20px;
          margin-bottom: 8px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .cart_btn {
          display: flex;
          justify-content: space-between;
          position: relative;
          .all_cart {
            width: 138px;
            height: 24px;
            position: absolute;
            left: 0;
            top: 24px;
          }
          .add_num {
            margin-top: 24px;
            height: 24px;
            display: flex;
            position: relative;
            .add_num_btn {
              width: 32px;
              height: 24px;
              margin-top: 0;
              margin-right: 5px;
              cursor: pointer;
            }
            .add_num_num {
              width: 58px;
              height: 24px;
              line-height: 24px;
              text-align: center;
              border-radius: 1.5px;
              font-size: 14px;
              color: #333333;
              margin: 0 5px;
            }
          }
          .btn_del {
            border: 1px solid rgba(0, 0, 0, 0.15);
            border-radius: 2px;
            width: 66px;
            height: 24px;
            line-height: 24px;
            text-align: center;
            cursor: pointer;
            img {
              width: 12px;
              height: 12px;
              margin-right: 4px;
            }
          }
        }
      }
    }
  }
  .admit {
    width: 98px;
    height: 32px;
    background: #0695d4;
    border-radius: 2px;
    font-size: 14px;
    color: #ffffff;
    text-align: center;
    line-height: 32px;
    margin: 45px 0 36px 790px;
    cursor: pointer;
  }
}
</style>



